import { FC, PropsWithChildren, forwardRef } from 'react';
import { DefaultProps, Text as MantineText } from '@mantine/core';
import { FormattedColor, TwBaseProps, TwPolymorphicComponent, TwStyleSystemProps } from '../../types';
import { styled } from '../../utils';
import { PropsFrom } from '../../utility-types';

type MantineTextProps = PropsFrom<typeof MantineText>

export interface TextProps
  extends TwBaseProps,
    TwStyleSystemProps,
    TwPolymorphicComponent,
    PropsWithChildren,
    Omit<MantineTextProps, keyof DefaultProps | 'c'> {
  c?: FormattedColor;
  as?: 'div' | 'p' | 'span' | 'legend';
}

const Root = styled(MantineText)<MantineTextProps>(() => ({}));

export const Text: FC<TextProps> = forwardRef((props, ref) => {
  const { as = 'div', span, ...rest } = props
  return <Root {...rest} component={span ? 'span' : as} ref={ref} />;
});
